.search-container {
    display: flex;
    align-items: center;
    background-color: white;
    color: #000;
    
    /* padding: 1rem 2rem; */
    padding-left: 1px;
    padding-right: 10px;
    border: 2px solid black;
    border-radius: 1rem;
  }
  
  .main-container.dark .search-container {
    background-color: var(--dark-blue-elements);
  }
  
  .search-icon {
    margin-right: 4px;
    margin-left: 10px;
  }
  
  .main-container.dark .search-icon {
    color: var(--color-white);
  }
  
  .search-container form {
    flex: 1;
  }
  
  .search-input {
    font-family: "Nunito Sans", sans-serif;
  
    border: none;
    width: 100%;
  
    padding: 1rem;
  }
  
  .main-container.dark .search-input {
    background-color: white;
    color: var(--color-white);
  }
  
  .main-container.dark .search-input::placeholder {
    color: var(--color-white);
  }
  
  .search-input:focus {
    outline: none;
  }
  